body {
    font: 14px "Century Gothic", Futura, sans-serif;
    font-family: Arial, Helvetica, sans-serif;
    margin: 20px;
  }
  
  ol, ul {
    padding-left: 30px;
  }

button {
  font-family: 'Courier New', Courier, monospace;
  cursor: pointer;
}

  .board-row:after {
    clear: both;
    content: "";
    display: table;
  }
  
  .status {
    margin-bottom: 10px;
  }
  
  .square {
    background: #fff;
    border: 1px solid #999;
    float: left;
    font-size: 24px;
    font-weight: bold;
    line-height: 34px;
    height: 34px;
    margin-right: -1px;
    margin-top: -1px;
    padding: 0;
    text-align: center;
    width: 34px;
  }
  
  .square:focus {
    outline: none;
  }

  .win {
    color: red;
    background: #ddd; 
  }
  
  .kbd-navigation .square:focus {
    background: #ddd;
  }
  
  .game {
    display: flex;
    flex-direction: row;
  }
  
  .game-info {
    margin-left: 20px;
  }

  .bold {
    font-weight: 900;
    color: darkred;
  }
  .step {
    display: flex;
  }
  .step button {
    text-align: left;
    width: 175px;
  }

  .shot-board {
    padding: 2px 0;
    padding-left: 5px;
    display: none;
    font-family: 'Courier New', Courier, monospace;
    font-weight: 900;
    letter-spacing: 5px;
    line-height: 75%;
    background: #eee;
    outline: 1px solid #999;
    white-space: pre-wrap;
  }

  .step:hover .shot-board {
    display:inline;
  }